


















































interface RefBase {
  dialogVisible: boolean;
  id: any;
  relaId: any;
  titleText: string;
  ruleForm: any;
  reset: any;
  addIndex: -1;
}
import {
  optDeleteEvent, // 删除
  taskDeleteEvent, // 删除
  configDeleteEvent, // 删除
  reminderDeleteEvent // 删除
} from '@api/taskLibraryManage';
import { Component, Vue, Ref, Prop, Watch } from 'vue-property-decorator'; // Component, Vue, Ref, Prop, Watch
import dealOption from './dealOption.vue';
import dealTask from './dealTask.vue';
import servicePush from './servicePush.vue';
import noticeDialog from './noticeDialog.vue';

import { namespace } from 'vuex-class';
import cloneDeep from 'lodash/cloneDeep';
const taskLibraryManage = namespace('taskLibraryManage');
@Component({
  components: { dealOption, dealTask, servicePush, noticeDialog }
})
export default class EventRegisterOptions extends Vue {
  @taskLibraryManage.State((state) => state.eventRegistPool) eventRegistPool;
  @taskLibraryManage.State((state) => state.flag) flag;
  @taskLibraryManage.Mutation('UPDATEEVENTREGISTPOOL') updateEventRegistpool;

  @Ref() refdealTask!: RefBase;
  @Ref() refServicePush!: RefBase;
  @Ref() refDealOption!: RefBase;
  @Ref() refNoticeDialog!: RefBase;
  @Prop() relaId;
  @Prop() registtabActive;

  // @Watch('eventRegistPool', { deep: true, immediate: true })
  // eventRegistPoolChange(val) {
  //   console.log('eventRegistPooleventRegistPooleventRegistPool', val);
  //   this.options = val[this.registtabActive] || [];
  // }

  @Watch('flag')
  sssChange(val, oldVal) {
    this.options = this.eventRegistPool[this.registtabActive] || [];
  }

  // get eventRegistP() {
  //   return this.$store.state.taskLibraryManage.eventRegistPool;
  // }

  @Watch('registtabActive', { deep: true, immediate: true })
  registtabActiveChange(val) {
    this.options = this.eventRegistPool[val] || [];
  }
  columns() {
    const _this = this;
    const trans = {
      0: '执行另一个操作',
      1: '启动另一个任务',
      2: '执行推送服务',
      3: '消息提醒'
    };
    return [
      {
        prop: '',
        type: 'index',
        align: 'center',
        label: '序号',
        width: '60'
      },
      {
        prop: 'registerName',
        label: '任务名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'eventType',
        label: '事件类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100',
        render(h: any, { record }: any) {
          return h('span', trans[record.eventType]);
        }
      },
      {
        prop: 'registerName',
        label: '操作类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'registerName',
        label: '操作',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100',
        actions: [
          {
            code: 'edit',
            text: '编辑',
            onClick: _this.handleEdit
          },
          {
            code: 'del',
            text: '删除',
            onClick: _this.handleDel
          }
        ]
      }
    ];
  }
  options: Array<any> = [];

  dropVal = '';
  dealOptionVisible = false;

  currentType = '';
  currentNamw = '';
  handleDel(item: any, index: any) {
    this.$confirm('确定删除?', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    })
      .then(() => {
        const id = item.id;
        // 执行另一个操作
        if (item.eventType * 1 === 0) {
          optDeleteEvent({ id }).then((res) => {
            if (!res.error) {
              delListFun();
            }
          });
        } // 启动另一个任务
        if (item.eventType * 1 === 1) {
          taskDeleteEvent({ id }).then((res) => {
            if (!res.error) {
              delListFun();
            }
          });
        }
        // 执行推送服务
        if (item.eventType * 1 === 2) {
          configDeleteEvent({ id }).then((res) => {
            if (!res.error) {
              delListFun();
            }
          });
        }
        // 消息提醒
        if (item.eventType * 1 === 3) {
          reminderDeleteEvent({ id }).then((res) => {
            if (!res.error) {
              delListFun();
            }
          });
        }
        const that = this;
        function delListFun() {
          that.updateEventRegistpool({
            type: that.registtabActive,
            data: null,
            addIndex: index
          });
          //   that.$message({
          //     type: 'success',
          //     message: '删除成功!'
          //   });
        }
      })
      .catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
  }
  // handleData(val) {
  //   console.log(val);
  //   if (val && Object.prototype.toString.call(val.rule) === '[object String]') {
  //     val.rule = JSON.parse(val.rule);
  //   }
  //   return cloneDeep(val);
  // }
  handleEdit(item, index) {
    const { eventType, id } = item;
    //发起另一个流程
    if (eventType === 0) {
      this.refdealTask.dialogVisible = true;
      this.refdealTask.id = id;
      this.refdealTask.relaId = this.relaId;
      this.refdealTask.addIndex = index;
      // this.refdealTask.ruleForm = this.handleData(item);
    }
    // 启动另一个任务
    if (eventType === 1) {
      this.refDealOption.dialogVisible = true;
      this.refDealOption.id = id;
      this.refDealOption.relaId = this.relaId;
      this.refDealOption.addIndex = index;
      // this.refDealOption.ruleForm = this.handleData(item);
    }
    //执行推送服务
    if (eventType === 2) {
      this.refServicePush.dialogVisible = true;
      this.refServicePush.id = id;
      this.refServicePush.relaId = this.relaId;
      this.refServicePush.addIndex = index;
      // this.refServicePush.ruleForm = this.handleData(item);
    }
    // 通知设置
    if (eventType === 3) {
      this.refNoticeDialog.dialogVisible = true;
      this.refNoticeDialog.id = id;
      this.refNoticeDialog.relaId = this.relaId;
      this.refNoticeDialog.addIndex = index;
      // this.refNoticeDialog.ruleForm = this.handleData(item);
    }
  }
  optionsCge(val: Array<any>) {
    this.options = [];
    if (!val || val.length === 0) {
      return;
    }
    val.forEach((item) => {
      this.options.push(JSON.parse(item));
    });
    // this.changelist(this.options); // 改变vuex中的当前状态列表用于下次添加时调取最新的list
  }
  changeDrop(val: string) {
    if (val === '1') {
      this.refdealTask.dialogVisible = true;
      this.refdealTask.id = null;
      this.refdealTask.addIndex = -1;
      this.refdealTask.relaId = this.relaId;
    }
    if (val === '2') {
      this.refDealOption.dialogVisible = true;
      this.refDealOption.id = null;
      this.refDealOption.addIndex = -1;
      this.refDealOption.relaId = this.relaId;
    }
    if (val === '3') {
      this.refServicePush.dialogVisible = true;
      this.refServicePush.id = null;
      this.refServicePush.addIndex = -1;
      this.refServicePush.relaId = this.relaId;
    }
    // 通知设置
    if (val === '4') {
      this.refNoticeDialog.dialogVisible = true;
      this.refNoticeDialog.id = null;
      this.refNoticeDialog.addIndex = -1;
      this.refNoticeDialog.relaId = this.relaId;
    }
  }
}
