





























import { Vue, Component, Prop, Watch, Ref } from 'vue-property-decorator';
import { viewTask } from '@api/taskLibraryManage';

import basicInfo from './addtasksConfig/basicInfo.vue'; // 基本信息
import launchingMode from './addtasksConfig/launchingMode.vue'; // 发起方式
import handleList from './addtasksConfig/handleList.vue'; // 操作列表

import { cloneDeep } from 'lodash';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');

interface AllData {
  taskLib: Record<string, any>;
}

@Component({
  components: { basicInfo, handleList, launchingMode }
})
export default class AddTasks extends Vue {
  name: 'AddTasks';
  @taskLibraryManage.Action('saveTask') saveTask; // 保存任务

  @taskLibraryManage.Mutation('UPDATEId') updateId; // 修改基本信息id
  @taskLibraryManage.Mutation('UPDATE_BASICINFO') updateBasicinfo; //更新基本信息
  @taskLibraryManage.Mutation('UPDATETIMEOUTREMIND') updateTimeOutRemind; //更新基本信息
  @taskLibraryManage.Mutation('UPDATETAGS') updatTags; //更新标签
  @taskLibraryManage.Mutation('INIT_LAUNCHING') init_launching; // 更新发起方式
  @taskLibraryManage.Mutation('UPDATETASKFORM') updateTaskForm; // 更新发起方式

  @Ref() baseInfo: any; // 新增 修改 复制

  private activeName = 'basicInfo';
  private loading = false;
  private allData: AllData = {
    taskLib: {}
  };

  back() {
    this.clearStore();
    const { scenesId } = this.$route.query;
    if (!scenesId) {
      this.$router.push({
        path: '/taskConfigCenter/businessScenarioManage'
      });
    } else {
      this.$router.push({
        path: '/taskConfigCenter/taskLibraryManage',
        query: { ...this.$route.query }
      });
    }
    // this.$router.back();
  }
  // 任务详情配置
  taskDetailSetting() {
    this.$router.push({
      path: '/taskConfigCenter/taskDetail',
      query: { ...this.$route.query }
    });
  }
  saveData() {
    const flag = this.baseInfo.validData();
    if (flag) {
      this.$nextTick(() => {
        // this.$store.dispatch('taskLibraryManage/saveTask');
        this.saveTask(this.$route.query).then((res) => {
          console.log(res);
          if (res && res.error) {
            this.$message.error(`保存失败:${res.error.message}`);
          } else {
            this.$message.success('保存成功');
            // 保存任务后才能新增操作列表 。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。。
            // 所以保存任务后修改store 里 tasklib的id
            this.updateId(res.data);
          }
        });
      });
    }
  }
  async loadData() {
    this.loading = true;
    const data = this.$route.query;

    // 如果是项目场景 多穿一个字段
    if (data.scenesType === '2') {
      data.relType = '3';
    }
    console.log('初始数据', data);
    const { id, copyId, isBack } = this.$route.query; // 修改数据id
    if (isBack) {
      const {
        taskLib,
        taskExternalList,
        taskHandList,
        taskTimingList,
        tags,
        timeOutRemind
      } = this.$store.state.taskLibraryManage;
      const data: any = {
        taskLib,
        tags,
        timeOutRemind,
        taskLaunching: {
          taskExternalList,
          taskHandList,
          taskTimingList
        }
      };
      this.allData = data;
      Object.assign(this.allData, cloneDeep(data));
      this.loading = false;
    } else if (id) {
      const data = { id };
      viewTask(data).then((res) => {
        console.log(res);
        if (!res.err) {
          // if (copyId) {
          //   res.data.taskLib.id = '';
          // }
          this.allData = res.data;
          Object.assign(this.allData, cloneDeep(data));
          console.log('全数据1', this.allData);
          this.storeData(res.data);
          !isBack && this.storeTaskForm(res.data);
          this.loading = false;
        } else {
          this.$message.error(`访问失败:${res.error.message}`);
          this.loading = false;
        }
      });
    } else {
      this.clearStore();
      Object.assign(this.allData, cloneDeep(data));
      this.loading = false;
    }

    console.log('全数据', this.allData);
  }

  storeData(data) {
    // 更新发起方式 外部指令发起
    data.taskLaunching.taskExternalList.length &&
      this.init_launching({
        type: 'taskExternalList',
        data: cloneDeep(data.taskLaunching.taskExternalList)
      });
    // 更新发起方式 手工发起
    data.taskLaunching.taskHandList.length &&
      this.init_launching({
        type: 'taskHandList',
        data: cloneDeep(data.taskLaunching.taskHandList)
      });
    // 更新发起方式 定时更新
    data.taskLaunching.taskTimingList.length &&
      this.init_launching({
        type: 'taskTimingList',
        data: cloneDeep(data.taskLaunching.taskTimingList)
      });
    this.updateBasicinfo(cloneDeep(data.taskLib));
    this.updateTimeOutRemind(cloneDeep(data.timeOutRemind));
    this.updatTags(cloneDeep(data.tags));
  }
  storeTaskForm(data) {
    this.updateTaskForm(cloneDeep(JSON.parse(data.taskForm)));
  }

  created() {
    this.loadData();
  }
  // 清空vuex
  clearStore() {
    const arr = ['taskExternalList', 'taskHandList', 'taskTimingList'];
    arr.forEach((item) => {
      this.init_launching({
        type: item,
        data: []
      });
    });
    this.updateTaskForm({});
  }
}
