























































































import paramsView from '../lanchConfig/paramsView.vue';
import { Component, Vue, Prop, Ref, Watch } from 'vue-property-decorator';

import {
  OptSaveEvent,
  OptUpdateEvent,
  OptViewEvent,
  listRelaName
} from '@api/taskLibraryManage';
import decisionRuleEditor from '../lanchConfig/decisionRuleEditor.vue';
import scriptEdit from '../operationSet/scriptEdit.vue';

import { arrayFind } from '@utils/util';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');

@Component({
  components: { decisionRuleEditor, scriptEdit, paramsView }
})
export default class DealTask extends Vue {
  @taskLibraryManage.Mutation('UPDATEEVENTREGISTPOOL') updateEventRegistpool;
  @taskLibraryManage.Getter('flowVariables') flowVariables: any;
  @Prop() registtabActive;
  @Prop() addIndex; // 修改index
  @Ref() treeRef: any;
  @Ref() ruleFormRef: any;

  dialogVisible = false;
  private handleOptions: Array<any> = []; // 操作选择
  ruleForm: any = {
    registerName: '',
    relaKey: '',
    rule: [],
    id: '',
    params: []
  };

  rules: any = {
    registerName: [
      { required: true, message: '请输入注册名称', trigger: 'blur' }
    ],
    relaKeyType: [
      { required: true, message: '请选择操作类型', trigger: 'change' }
    ],
    relaKey: [{ required: true, message: '请选择操作选择', trigger: 'change' }]
  };

  private trans = {
    startUpEvent: 0,
    readyEvent: 1,
    startEvent: 2,
    doingEvent: 3,
    successEvent: 4,
    failEvent: 5,
    endEvent: 6,
    operationEndEvent: 7
  };
  id: any = null;
  @Watch('id')
  idChange(val) {
    if (val) {
      OptViewEvent({ id: val }).then((res) => {
        if (!res.error) {
          let rule: Array<any> = [];
          if (typeof res.rule === 'string') {
            rule = JSON.parse(res.rule);
          }
          this.ruleForm = { ...res.data, rule };
        } else {
          this.$message.error(`访问失败${res.error.message}`);
        }
      });
    }
  }
  relaId: any = null;
  private dealOptions: Array<any> = [
    {
      value: 0,
      label: '线下办理'
    },
    {
      value: 1,
      label: '线上办理'
    },
    {
      value: 2,
      label: '检查项调用'
    },
    {
      value: 3,
      label: 'RPA调用'
    },
    {
      value: 4,
      label: 'OCR调用'
    },
    {
      value: 5,
      label: '多步骤操作'
    }
  ];

  private paymentList: Array<any> = []; // 绑定参数
  private dialogtree = false;
  private currentParam: any = {};
  treeName: any = '';
  treeValue: any = '';

  columns() {
    const _self = this;
    return [
      {
        label: '序号',
        type: 'index',
        align: 'center',
        width: '70'
      },
      {
        prop: 'paramCode',
        label: '参数code',
        align: 'center',
        'min-width': '80'
      },
      {
        prop: 'paramName',
        align: 'center',
        label: '参数名称',
        'min-width': '80'
      },
      {
        prop: 'paramType',
        align: 'center',
        label: '参数类型',
        // sortable: "custom",
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.paramType * 1 === 0) {
            return h('span', '字符串');
          } else if (record.paramType * 1 === 1) {
            return h('span', '数字');
          } else if (record.paramType * 1 === 2) {
            return h('span', '日期');
          } else if (record.paramType * 1 === 3) {
            return h('span', '布尔');
          }
        }
      },
      {
        prop: 'paramValueName',
        label: '参数值',
        // sortable: "custom",
        align: 'center',
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.treeCode === true) {
            return h('span', [
              h('span', record.paramValueName),
              h('el-button', {
                props: {
                  type: 'text',
                  icon: 'el-icon-delete'
                },
                style: 'margin-left: 10px',
                on: {
                  click: () => _self.deleteFiled(record)
                }
              })
            ]);
          } else if (record.treeButton === true || record.paramValueName) {
            return h('span', record.paramValueName);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _self.setFiled(record)
                  }
                },
                '设置'
              )
            ]);
          }
        }
      }
    ];
  }

  reset() {
    this.ruleForm = {
      registerName: '',
      relaKeyType: '',
      relaKey: '',
      rule: [],
      id: '',
      type: 1,
      params: []
    };
    this.ruleFormRef.resetFields();
  }

  cancelDialog() {
    this.id = '';
    this.reset();
    this.dialogVisible = false;
  }

  submitForm(formName: any) {
    const _ref: any = this.$refs;
    _ref[formName].validate((valid: any) => {
      if (valid) {
        const rule = JSON.stringify(this.treeRef.getCurrentRule());
        const datas = {
          ...this.ruleForm,
          rule,
          id: this.id,
          relaId: this.relaId,
          type: 1,
          taskId: this.$route.query.id || this.$route.query.copyId,
          eventType: 0,
          executionType: this.trans[this.registtabActive]
        };
        console.log(datas);
        const data = JSON.parse(JSON.stringify(datas));
        if (!this.id) {
          OptSaveEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: -1
              });
              this.cancelDialog();
            } else {
              this.$message.error(`新增失败${res.error.message}`);
            }
          });
        } else {
          OptUpdateEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: this.addIndex
              });
              this.cancelDialog();
            } else {
              this.$message.error(`新增失败${res.error.message}`);
            }
          });
        }
      } else {
        return false;
      }
    });
  }

  getRelaId() {
    this.$set(this.ruleForm, 'relaKey', '');
    const data = {
      relaType: this.ruleForm.relaKeyType,
      taskId: this.$route.query.id
    };
    listRelaName(data).then((res) => {
      this.handleOptions = res.data;
    });
  }

  regionCge(val: any) {
    this.ruleForm.params = [];
    const server = arrayFind(this.handleOptions, 'relaId', val);
    console.log(server);
    if (server && server.taskRelaParams) {
      this.ruleForm.params = server.taskRelaParams.map((param) => {
        return {
          paramCode: param.paramCode,
          paramName: param.paramName,
          paramType: param.paramType,
          paramValueName: param.paramDefault,
          paramValue: param.paramValue
        };
      });
    }
  }
  // 删除
  deleteFiled(row: any) {
    this.$delete(row, 'treeButton');
    this.$delete(row, 'treeCode');
    row.paramValueName = '';
    row.paramValue = '';
  }

  setFiled(record: any) {
    this.currentParam = record;
    this.dialogtree = true;
  }
  canceltreeDialog() {
    this.dialogtree = false;
  }
  handleNodeClick(node: any) {
    this.treeName = node.label;
    this.treeValue = node.value;
  }
  setDetermines() {
    this.$set(this.currentParam, 'treeButton', true);
    this.$set(this.currentParam, 'paramValueName', this.treeName);
    this.$set(this.currentParam, 'paramValue', this.treeValue);
    this.dialogtree = false;
  }

  cellMouseEnter(row: any, column: any) {
    if (column.property === 'paramValueName' && row.paramValueName) {
      this.$delete(row, 'treeButton');
      this.$set(row, 'treeCode', true);
    }
  }

  cellMouseleave(row: any) {
    if (row.paramValueName) {
      this.$delete(row, 'treeCode');
      this.$set(row, 'treeButton', true);
    }
  }
}
