


























































import handleListDia from './handleListDia.vue';
import moment from 'moment';
import { Vue, Component, Watch } from 'vue-property-decorator';
import {
  deleteBatchTaskRela,
  deleteTaskRela,
  listTaskRela
} from '@api/taskLibraryManage';
import cloneDeep from 'lodash/cloneDeep';
@Component({
  components: {
    handleListDia
  }
})
export default class HandleList extends Vue {
  name: 'HandleList';
  private searchData: Record<string, any> = {
    relaName: ''
    // relaType: ''
  };
  private tableData: Array<any> = [];
  private total = 0;
  private page = 1;
  private limit = 10;
  private loading = false;
  private selections: Array<any> = [];
  private data: Record<string, any> = {};
  private basicOptions = [
    {
      value: 0,
      label: '线下办理'
    },
    {
      value: 1,
      label: '线上办理'
    },
    {
      value: 2,
      label: '检查项调用'
    },
    {
      value: 3,
      label: 'RPA调用'
    },
    {
      value: 4,
      label: 'OCR调用'
    },
    {
      value: 5,
      label: '多步骤操作'
    }
  ];
  private isCopy = false;
  columns() {
    const _this = this;
    const typeTrans = {
      0: '线下办理',
      1: '线上办理',
      2: '检查项调用',
      3: 'RPA调用',
      4: 'OCR调用',
      5: '多步骤操作'
    };
    return [
      {
        prop: '',
        type: 'selection',
        label: '序号',
        width: '60'
      },
      {
        prop: 'relaName',
        label: '操作名称',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '100'
      },
      {
        prop: 'relaType',
        label: '操作类型',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '120',
        render: (h, record) => {
          return h('span', typeTrans[record.record.relaType]);
        }
      },
      {
        prop: 'createTime',
        label: '操作时间',
        'header-align': 'left',
        showOverflowTooltip: true,
        'min-width': '80',
        render: (h, record) => {
          return h(
            'span',
            moment(record.record.createTime).format('YYYY-MM-DD HH:mm:ss')
          );
        }
      },
      {
        prop: '',
        align: 'center',
        label: '操作',
        width: '390',
        headerAlign: 'center',
        actions: [
          {
            code: 'edit',
            text: '修改',
            onClick: (row) => _this.edit(row)
          },
          {
            code: 'copy',
            text: '复制',
            onClick: (row) => _this.copy(row)
          },
          {
            code: 'del',
            text: '删除',
            onClick: (row) => _this.del(row)
          }
        ]
      }
    ];
  }
  private visible = false;

  created() {
    this.loadData('init');
  }
  // 获取数据
  loadData(params = '') {
    this.loading = true;
    if (params) {
      this.page = 1;
    }
    const { limit, page, searchData } = this;
    const data = {
      limit,
      page,
      taskId: this.$route.query.id,
      ...searchData
    };
    listTaskRela(data).then((res) => {
      this.loading = false;
      if (!res.error) {
        this.tableData = res.data.rows;
        this.total = res.data.total;
      } else {
        this.$message.error(`访问失败:${res.error.message}`);
      }
    });
  }
  // 新增
  addItem() {
    this.data = {};
    this.visible = true;
  }
  // 弹窗关闭
  cancelVisible() {
    this.isCopy = false;
    this.visible = false;
    this.loadData('init');
  }
  // 修改
  edit(row: Record<string, any>) {
    this.data = cloneDeep(row);
    this.visible = true;
  }
  // 复制
  copy(row: Record<string, any>) {
    this.data = cloneDeep(row);
    this.isCopy = true;
    this.visible = true;
  }
  // 删除
  del(row: Record<string, any>) {
    const data = { id: row.id };
    deleteTaskRela(data).then((res) => {
      if (res.error) {
        this.$message.success('删除失败');
      } else {
        this.loadData('init');
      }
    });
  }
  // 多选删除
  delMore() {
    deleteBatchTaskRela(this.selections).then((res) => {
      if (res.error) {
        this.$message.success('删除失败');
      } else {
        this.loadData('init');
      }
    });
  }
  // 页数
  handleCurrent(val) {
    this.page = val;
    this.loadData();
  }
  // 页码大小
  handleSize(val) {
    this.limit = val;
    this.page = 1;
    this.loadData();
  }
  // 多选
  selectionChange(val) {
    this.selections = [];
    val.map((item: any) => {
      this.selections.push(item.id);
    });
    console.log(this.selections);
  }
}
