







































































































import decisionRuleEditor from './decisionRuleEditor.vue';
import { Component, Vue, Prop, Watch, Ref } from 'vue-property-decorator';
import cloneDeep from 'lodash/cloneDeep';
// const taskLibraryManage = namespace('taskLibraryManage');
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');

@Component({
  components: { decisionRuleEditor }
})
export default class Manual extends Vue {
  @Prop({ default: 'manual' }) launching: string;
  @Ref() ruleForm;
  @taskLibraryManage.State('taskHandList') taskHandList;
  @taskLibraryManage.Mutation('UPDATE_LAUNCHING') update_launching;
  @taskLibraryManage.Mutation('DEL_LAUNCHING') del_launching;
  private rules: any = {
    handName: [{ required: true, message: '请输入名称', trigger: 'blur' }]
  };
  private manualForm: Record<string, any> = {
    handName: '',
    allowSplit: 0,
    dataViewId: '',
    ruleArray: [],
    isInstances: '',
    isInstancesCondition: ''
  };
  private editIndex = -1;
  private showEditPanel = false;
  private allowSplitOptions: Array<any> = [
    {
      value: 0,
      label: '拆分'
    },
    {
      value: 1,
      label: '不拆分'
    }
  ];
  private isInstancesOptions: Array<any> = [
    {
      value: 0,
      label: '否'
    },
    {
      value: 1,
      label: '是'
    }
  ];
  private isInstancesConditionOptions: Array<any> = [
    {
      value: 0,
      label: '全量'
    },
    {
      value: 1,
      label: '当日'
    }
  ];

  get manualFormList() {
    return this.taskHandList;
  }

  addItem() {
    this.resetForm();
    this.showEditPanel = true;
  }
  editItem(item, index) {
    this.showEditPanel = true;
    this.manualForm = cloneDeep(item);
    this.editIndex = index;
  }
  delItem(item, index) {
    this.del_launching({
      type: 'taskHandList',
      index
    });
    this.resetForm();
  }
  saveData() {
    this.ruleForm.validate((valid) => {
      if (valid) {
        console.log(this.manualForm);
        const rules: any = this.$refs.rules;
        this.manualForm = { ...this.manualForm, ...rules.getCurrentRule() };
        console.log(this.manualForm);

        const { editIndex, manualForm } = this;
        this.update_launching({
          type: 'taskHandList',
          data: cloneDeep(manualForm),
          editIndex
        });
        this.$nextTick(() => {
          this.editIndex = -1;
          this.resetForm();
        });
      } else {
        return false;
      }
    });
  }
  resetForm() {
    this.manualForm = {
      handName: '',
      allowSplit: 0,
      dataViewId: '',
      ruleArray: [],
      isInstances: '',
      isInstancesCondition: ''
    };
  }
}
