















































































import paramsView from '../lanchConfig/paramsView.vue';
import { Component, Vue, Prop, Watch, Ref } from 'vue-property-decorator';

import scriptEdit from '../operationSet/scriptEdit.vue';
import decisionRuleEditor from '../lanchConfig/decisionRuleEditor.vue';

import { dataViewFields } from '@api/viewData';
import { arrayFind } from '@utils/util';
import {
  configSaveEvent,
  configUpdateEvent,
  configViewEvent
} from '@api/taskLibraryManage';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');

@Component({
  components: { decisionRuleEditor, scriptEdit, paramsView }
})
export default class ExecutePush extends Vue {
  @taskLibraryManage.Mutation('UPDATEEVENTREGISTPOOL') updateEventRegistpool;
  @taskLibraryManage.Getter('flowVariables') flowVariables: any;
  @Prop() optionsCge!: (val: Array<any>) => void;

  @Prop() registtabActive;
  @Ref() treeRef: any;
  @Ref() ruleFormRef: any;
  @Prop() relaId: any;
  @Prop() addIndex;

  id: any = null;
  @Watch('id')
  idChange(val) {
    if (val) {
      this.loading = true;
      configViewEvent({ id: val }).then((res) => {
        if (!res.error) {
          this.loading = false;
          this.ruleForm = res.data;
        } else {
          this.$message.error(`访问失败:${res.error.message}`);
          this.loading = false;
        }
      });
    }
  }
  dialogVisible = false;
  @Watch('dialogVisible')
  dialogVisibleChange(val) {
    if (val) {
      dataViewFields({ type: '1' }).then((res) => {
        this.serviceOptions = res.data;
      });
    }
  }
  private loading = false;
  private trans = {
    startUpEvent: 0,
    readyEvent: 1,
    startEvent: 2,
    doingEvent: 3,
    successEvent: 4,
    failEvent: 5,
    endEvent: 6,
    operationEndEvent: 7
  };
  private dialogtree = false;
  private currentParam: any = {};
  treeName: any = '';
  treeValue: any = '';

  columns() {
    const _self = this;
    return [
      {
        label: '序号',
        type: 'index',
        align: 'center',
        width: '70'
      },
      {
        prop: 'paramCode',
        label: '参数code',
        align: 'center',
        'min-width': '80'
      },
      {
        prop: 'paramName',
        align: 'center',
        label: '参数名称',
        'min-width': '80'
      },
      {
        prop: 'paramType',
        align: 'center',
        label: '参数类型',
        // sortable: "custom",
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.paramType * 1 === 0) {
            return h('span', '字符串');
          } else if (record.paramType * 1 === 1) {
            return h('span', '数字');
          } else if (record.paramType * 1 === 2) {
            return h('span', '日期');
          } else if (record.paramType * 1 === 3) {
            return h('span', '布尔');
          }
        }
      },
      {
        prop: 'paramValueName',
        label: '参数值',
        // sortable: "custom",
        align: 'center',
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.treeCode === true) {
            return h('span', [
              h('span', record.paramValueName),
              h('el-button', {
                props: {
                  type: 'text',
                  icon: 'el-icon-delete'
                },
                style: 'margin-left: 10px',
                on: {
                  click: () => _self.deleteFiled(record)
                }
              })
            ]);
          } else if (record.treeButton === true || record.paramValueName) {
            return h('span', record.paramValueName);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _self.setFiled(record)
                  }
                },
                '设置'
              )
            ]);
          }
        }
      }
    ];
  }

  private serviceOptions: Array<any> = [];
  ruleArray: any;
  ruleForm: any = {
    registerName: '',
    type: 1,
    params: [],
    scriptParams: []
  };
  rules: any = {
    registerName: [
      { required: true, message: '请输入事件名称', trigger: 'blur' }
    ],
    serviceId: [
      { required: true, message: '请选择推送服务', trigger: 'change' }
    ]
  };

  reset() {
    this.addIndex = -1;
    this.ruleForm = {
      serviceValue: '',
      params: [],
      scriptParams: []
    };
    this.ruleFormRef.resetFields();
  }
  cancelTreeDialog() {
    this.dialogtree = false;
  }
  cancelDialog() {
    this.id = '';
    this.reset();
    this.dialogVisible = false;
  }
  serviceIdChange() {
    const server = arrayFind(
      this.serviceOptions,
      'id',
      this.ruleForm.serviceId
    );
    if (server && server.queryParams) {
      this.ruleForm.params = server.queryParams.map((param) => {
        return {
          paramCode: param.code,
          paramName: param.name,
          paramType: param.type,
          paramValueName: param.defaultValue,
          paramValue: ''
        };
      });
    }
  }
  // 删除
  deleteFiled(row: any) {
    this.$delete(row, 'treeButton');
    this.$delete(row, 'treeCode');
    row.paramValueName = '';
    row.paramValue = '';
  }

  setFiled(record: any) {
    this.currentParam = record;
    this.dialogtree = true;
  }

  handleNodeClick(node: any) {
    this.treeName = node.label;
    this.treeValue = node.value;
  }
  setDetermines() {
    this.$set(this.currentParam, 'treeButton', true);
    this.$set(this.currentParam, 'paramValueName', this.treeName);
    this.$set(this.currentParam, 'paramValue', this.treeValue);
    this.dialogtree = false;
  }

  cellMouseEnter(row: any, column: any) {
    if (column.property === 'paramValueName' && row.paramValueName) {
      this.$delete(row, 'treeButton');
      this.$set(row, 'treeCode', true);
    }
  }

  cellMouseleave(row: any) {
    if (row.paramValueName) {
      this.$delete(row, 'treeCode');
      this.$set(row, 'treeButton', true);
    }
  }

  getHashCode(str: string) {
    let hash = 1315423911,
      i,
      ch;
    for (i = str.length - 1; i >= 0; i--) {
      ch = str.charCodeAt(i);
      hash ^= (hash << 5) + ch + (hash >> 2);
    }
    return `ep${hash & 0x7fffffff}`; //ep
  }
  submitForm(formName: any) {
    const _ref: any = this.$refs;
    _ref[formName].validate((valid: any) => {
      if (valid) {
        const datas = {
          ...this.ruleForm,
          eventType: 2,
          type: 1,
          code: this.getHashCode(this.ruleForm.registerName),
          id: this.id,
          relaId: this.relaId,
          executionType: this.trans[this.registtabActive],
          taskId: this.$route.query.id || this.$route.query.copyId
        };
        const data = JSON.parse(JSON.stringify(datas));
        if (!this.id) {
          configSaveEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: this.addIndex
              });
              this.cancelDialog();
            } else {
              this.$message.error(`新增失败:${res.error.message}`);
            }
          });
        } else {
          configUpdateEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: this.addIndex
              });
              this.cancelDialog();
            } else {
              this.$message.error(`修改失败:${res.error.message}`);
            }
          });
        }
      } else {
        return false;
      }
    });
  }
}
