









































































































































































import decisionRuleEditor from '../lanchConfig/decisionRuleEditor.vue';
import NumberInput from '@components/NumberInput/index.vue';
import { Component, Vue, Ref, Prop, Watch } from 'vue-property-decorator'; // Component, Vue, Ref, Prop, Watch
import { namespace } from 'vuex-class';
import cloneDeep from 'lodash/cloneDeep';
const taskLibraryManage = namespace('taskLibraryManage');

@Component({
  components: { decisionRuleEditor, NumberInput }
})
export default class HandleOptions extends Vue {
  @taskLibraryManage.Getter('flowVariables') flowVariables: any;
  @Prop() type;
  @Ref() endRuleRef;
  @Ref() treeRef;
  @Ref() ruleFormRef;
  @Prop() dealRequest;
  @Watch('dealRequest', { deep: true, immediate: true })
  dealRequestChange(val) {
    if (val && JSON.stringify(val) !== '{}') {
      console.log('valvalval', val);
      if (val.endRule && typeof val.endRule === 'string') {
        val.endRule = JSON.parse(val.endRule);
      }

      if (val.rule && typeof val.rule === 'string') {
        val.rule = JSON.parse(val.rule);
      }
      this.ruleForm = cloneDeep(val);
    } else {
      this.ruleForm = {
        isHand: 0,
        endDate: null,
        execTime: null,
        startDate: null,
        offsetUnit: 0,
        offsetDay: 0,
        startUnit: 1,
        startFreq: 0,
        noticeTitle: '',
        viewCode: '',
        noticeContent: '',
        endRule: [],
        rule: []
      };
    }
  }
  @Prop() paramRequest;
  @Watch('paramRequest', { deep: true, immediate: true })
  paramRequestChange(val) {
    if (val && val.length) {
      console.log(val);
      this.paymentList = cloneDeep(val);
    }
  }

  ruleForm: any = {
    isHand: 0,
    endDate: null,
    execTime: null,
    startDate: null,
    offsetUnit: 0,
    offsetDay: 0,
    startUnit: 1,
    startFreq: 0,
    noticeTitle: '',
    viewCode: '',
    noticeContent: '',
    endRule: [],
    rule: []
  };

  private rules = {};
  @Prop({ default: [] }) dataViewList;
  private paymentList: Array<any> = [];
  private currentParam: any = {};
  private dialogtree = false;
  treeName: any = '';
  treeValue: any = '';

  columns() {
    const _self = this;
    return [
      {
        label: '序号',
        type: 'index',
        align: 'center',
        width: '70'
      },
      {
        prop: 'paramCode',
        label: '参数code',
        align: 'center',
        'min-width': '80'
      },
      {
        prop: 'paramName',
        align: 'center',
        label: '参数名称',
        'min-width': '80'
      },
      {
        prop: 'paramType',
        align: 'center',
        label: '参数类型',
        // sortable: "custom",
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.paramType * 1 === 0) {
            return h('span', '字符串');
          } else if (record.paramType * 1 === 1) {
            return h('span', '数字');
          } else if (record.paramType * 1 === 2) {
            return h('span', '日期');
          } else if (record.paramType * 1 === 3) {
            return h('span', '布尔');
          }
        }
      },
      {
        prop: 'paramValueName',
        label: '参数值',
        // sortable: "custom",
        align: 'center',
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.treeCode === true) {
            return h('span', [
              h('span', record.paramValueName),
              h('el-button', {
                props: {
                  type: 'text',
                  icon: 'el-icon-delete'
                },
                style: 'margin-left: 10px',
                on: {
                  click: () => _self.deleteFiled(record)
                }
              })
            ]);
          } else if (record.treeButton === true || record.paramValueName) {
            return h('span', record.paramValueName);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _self.setFiled(record)
                  }
                },
                '设置'
              )
            ]);
          }
        }
      }
    ];
  }
  private endOffsetUnitOptions: Array<any> = [
    { value: '0', label: '工作日' },
    { value: '1', label: '自然日' }
  ];
  private reminderUnitOptions = [
    { value: '1', label: '分钟' },
    { value: '2', label: '小时' },
    { value: '3', label: '自然日' },
    { value: '4', label: '工作日' },
    { value: '5', label: '月' },
    { value: '6', label: '年' },
    { value: '7', label: '一次' }
  ];

  // 校验
  validForm() {
    let flag = true;
    if (this.type === 1) {
      if (this.ruleForm.startDate > this.ruleForm.endDate) {
        this.$message.error('操作配置内开始时间须早于结束时间');
        flag = false;
      }
    }
    return flag;
  }
  // form
  getDealRequest() {
    const rule = JSON.stringify(this.treeRef.getCurrentRule());
    const endRule = JSON.stringify(this.endRuleRef.getCurrentRule());
    return { ...this.ruleForm, rule, endRule };
  }
  // 表格
  getSetting() {
    console.log(this.paymentList);
    return this.paymentList;
  }

  deleteFiled(row: any) {
    this.$delete(row, 'treeButton');
    this.$delete(row, 'treeCode');
    row.paramValueName = '';
    row.paramValue = '';
  }

  setFiled(record: any) {
    this.currentParam = record;
    this.dialogtree = true;
  }

  handleNodeClick(node: any) {
    this.treeName = node.label;
    this.treeValue = node.value;
  }

  setDetermines() {
    this.$set(this.currentParam, 'treeButton', true);
    this.$set(this.currentParam, 'paramValueName', this.treeName);
    this.$set(this.currentParam, 'paramValue', this.treeValue);
    this.dialogtree = false;
  }
  cancelDialog() {
    this.dialogtree = false;
  }

  cellMouseEnter(row: any, column: any) {
    if (column.property === 'paramValueName' && row.paramValueName) {
      this.$delete(row, 'treeButton');
      this.$set(row, 'treeCode', true);
    }
  }
  cellMouseleave(row: any) {
    if (row.paramValueName) {
      this.$delete(row, 'treeCode');
      this.$set(row, 'treeButton', true);
    }
  }
}
