

















































































import paramsView from '../lanchConfig/paramsView.vue';
import { Component, Vue, Prop, Ref, Watch } from 'vue-property-decorator';

import {
  taskSaveEvent,
  taskUpdateEvent,
  taskViewEvent,
  listExternalTask // 获取任务列表
} from '@api/taskLibraryManage';
import { arrayFind } from '@utils/util';
import decisionRuleEditor from '../lanchConfig/decisionRuleEditor.vue';
import scriptEdit from '../operationSet/scriptEdit.vue';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');

@Component({
  components: { decisionRuleEditor, scriptEdit, paramsView }
})
export default class NoticeDialog extends Vue {
  @taskLibraryManage.Mutation('UPDATEEVENTREGISTPOOL') updateEventRegistpool;
  @taskLibraryManage.Getter('flowVariables') flowVariables: any;
  @Prop() optionsCge!: (val: Array<any>) => void;
  @Ref() treeRef: any;
  @Ref() ruleFormRef: any;
  @Prop() relaId: any;
  @Prop() registtabActive;

  @Prop() addIndex;
  private taskOptions: Array<any> = [];
  dialogVisible = false;
  @Watch('dialogVisible')
  dialogVisibleChange(val) {
    if (val) {
      const { scenesId } = this.$route.query;
      listExternalTask({ scenesId }).then((res) => {
        if (!res.error) {
          this.taskOptions = res.data;
        } else {
          this.$message.error(`任务列表访问失败:${res.error.message}`);
        }
      });
    }
  }
  ruleForm: any = {
    registerName: '',
    relaTaskId: '',
    rule: null,
    type: 1,

    params: []
  };

  rules: any = {
    registerName: [
      { required: true, message: '请输入注册名称', trigger: 'blur' }
    ]
  };
  id: any = null;
  @Watch('id')
  idChange(val) {
    if (val) {
      taskViewEvent({ id: val }).then((res) => {
        if (!res.error) {
          let rule: Array<any> = [];
          if (res.data.rule) {
            rule = JSON.parse(res.data.rule);
          }
          console.log(rule);
          this.ruleForm = { ...res.data, rule };
          console.log(this.ruleForm);
        } else {
          this.$message.error(`访问失败:${res.error.message}`);
        }
      });
    }
  }
  private trans = {
    startUpEvent: 0,
    readyEvent: 1,
    startEvent: 2,
    doingEvent: 3,
    successEvent: 4,
    failEvent: 5,
    endEvent: 6,
    operationEndEvent: 7
  };
  private currentParam: any = {};
  private dialogtree = false;
  treeName: any = '';
  treeValue: any = '';

  private dealOptions: Array<any> = [
    {
      value: 0,
      label: '线下办理'
    },
    {
      value: 1,
      label: '线上办理'
    },
    {
      value: 2,
      label: '检查项调用'
    },
    {
      value: 3,
      label: 'RPA调用'
    },
    {
      value: 4,
      label: 'OCR调用'
    },
    {
      value: 5,
      label: '多步骤操作'
    }
  ];

  columns() {
    const _self = this;
    return [
      {
        label: '序号',
        type: 'index',
        align: 'center',
        width: '70'
      },
      {
        prop: 'paramCode',
        label: '参数code',
        align: 'center',
        'min-width': '80'
      },
      {
        prop: 'paramName',
        align: 'center',
        label: '参数名称',
        'min-width': '80'
      },
      {
        prop: 'paramType',
        align: 'center',
        label: '参数类型',
        // sortable: "custom",
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.paramType * 1 === 0) {
            return h('span', '字符串');
          } else if (record.paramType * 1 === 1) {
            return h('span', '数字');
          } else if (record.paramType * 1 === 2) {
            return h('span', '日期');
          } else if (record.paramType * 1 === 3) {
            return h('span', '布尔');
          }
        }
      },
      {
        prop: 'paramDefault',
        label: '参数值',
        // sortable: "custom",
        align: 'center',
        'min-width': '80',
        render(h: any, { record }: any) {
          if (record.treeCode === true) {
            return h('span', [
              h('span', record.paramDefault),
              h('el-button', {
                props: {
                  type: 'text',
                  icon: 'el-icon-delete'
                },
                style: 'margin-left: 10px',
                on: {
                  click: () => _self.deleteFiled(record)
                }
              })
            ]);
          } else if (record.treeButton === true || record.paramDefault) {
            return h('span', record.paramDefault);
          } else {
            return h('span', [
              h(
                'el-button',
                {
                  props: {
                    type: 'text'
                  },
                  on: {
                    click: () => _self.setFiled(record)
                  }
                },
                '设置'
              )
            ]);
          }
        }
      }
    ];
  }
  reset() {
    this.ruleForm = {
      registerName: '',
      relaTaskId: '',
      rule: null,
      params: []
    };
    this.ruleFormRef.resetFields();
  }
  // 改变选择任务
  changeTaskId(val) {
    this.ruleForm.params = [];
    const server = arrayFind(this.taskOptions, 'taskId', val);
    if (server && server.taskExternalParamList) {
      this.ruleForm.params = server.taskExternalParamList.map((param) => {
        return {
          paramCode: param.paramCode,
          paramName: param.paramName,
          paramType: param.paramType,
          paramDefault: param.paramDefault,
          paramValue: ''
        };
      });
    }
  }

  deleteFiled(row: any) {
    this.$delete(row, 'treeButton');
    this.$delete(row, 'treeCode');
    row.paramDefault = '';
    row.paramValue = '';
  }

  setFiled(record: any) {
    this.currentParam = record;
    this.dialogtree = true;
  }

  handleNodeClick(node: any) {
    this.treeName = node.label;
    this.treeValue = node.value;
  }

  setDetermines() {
    this.$set(this.currentParam, 'treeButton', true);
    this.$set(this.currentParam, 'paramDefault', this.treeName);
    this.$set(this.currentParam, 'paramValue', this.treeValue);
    this.dialogtree = false;
  }
  cellMouseEnter(row: any, column: any) {
    if (column.property === 'paramDefault' && row.paramDefault) {
      this.$delete(row, 'treeButton');
      this.$set(row, 'treeCode', true);
    }
  }
  cellMouseleave(row: any) {
    if (row.paramDefault) {
      this.$delete(row, 'treeCode');
      this.$set(row, 'treeButton', true);
    }
  }

  // 取消
  cancelsetDetermines() {
    this.dialogtree = false;
  }

  // 取消
  cancelDialog() {
    this.id = '';
    this.reset();
    this.dialogVisible = false;
  }
  // 提交
  submitForm(formName: any) {
    const _ref: any = this.$refs;
    _ref[formName].validate((valid: any) => {
      if (valid) {
        const rule = JSON.stringify(this.treeRef.getCurrentRule());
        const datas = {
          id: this.id,
          ...this.ruleForm,
          relaId: this.relaId,
          type: 1,
          taskId: this.$route.query.id || this.$route.query.copyId,
          rule,
          eventType: 1,
          executionType: this.trans[this.registtabActive]
        };
        const data = JSON.parse(JSON.stringify(datas));
        if (!this.id) {
          // 新增
          taskSaveEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: -1
              });
              this.cancelDialog();
            } else {
              this.$message.error(`新增失败:${res.error.message}`);
            }
          });
        } else {
          taskUpdateEvent(data).then((res) => {
            if (!res.error) {
              data.id = res.data;
              this.updateEventRegistpool({
                type: this.registtabActive,
                data,
                addIndex: this.addIndex
              });
              this.cancelDialog();
            } else {
              this.$message.error(`新增失败:${res.error.message}`);
            }
          });
        }
      } else {
        return false;
      }
    });
  }
}
