


























































































import paramsView from './paramsView.vue';

import { Component, Vue, Prop, Ref } from 'vue-property-decorator';
import cloneDeep from 'lodash/cloneDeep';
import { namespace } from 'vuex-class';
const taskLibraryManage = namespace('taskLibraryManage');
@Component({
  components: { paramsView }
})
export default class Instruction extends Vue {
  @Ref() ruleForm;
  @Prop({ default: 'manual' }) launching: string;
  @taskLibraryManage.State('taskExternalList') taskExternalList;
  @taskLibraryManage.Mutation('UPDATE_LAUNCHING') update_launching;
  @taskLibraryManage.Mutation('DEL_LAUNCHING') del_launching;

  private instructionForm: Record<string, any> = {
    externalName: '',
    externalKey: '',
    allowSplit: 0,
    isInstances: '',
    isInstancesCondition: '',
    taskExternalParamList: [
      {
        id: 1,
        paramCode: '',
        paramName: '',
        paramType: '',
        required: '',
        paramDefault: '',
        edits: [0, 1, 1, 1, 1, 1, 0],
        addButton: true
      }
    ]
  };

  private isInstancesOptions: Array<any> = [
    {
      value: 0,
      label: '否'
    },
    {
      value: 1,
      label: '是'
    }
  ];
  private isInstancesConditionOptions: Array<any> = [
    {
      value: 0,
      label: '全量'
    },
    {
      value: 1,
      label: '当日'
    }
  ];
  private rules: any = {
    externalName: [{ required: true, message: '请输入名称', trigger: 'blur' }]
  };
  private editIndex = -1;
  private showEditPanel = false;

  get instructionFormList() {
    return this.taskExternalList;
  }
  addItem() {
    this.resetForm();
    this.showEditPanel = true;
  }
  editItem(item, index) {
    console.log(item);
    this.showEditPanel = true;
    this.instructionForm = cloneDeep(item);
    if (!item.taskExternalParamList.length)
      item.taskExternalParamList = [
        {
          id: 1,
          paramCode: '',
          paramName: '',
          paramType: '',
          required: '',
          paramDefault: '',
          edits: [0, 1, 1, 1, 1, 1, 0],
          addButton: true
        }
      ];
    this.editIndex = index;
  }
  delItem(item, index) {
    this.del_launching({
      type: 'taskExternalList',
      index
    });
    this.resetForm();
  }
  saveData() {
    this.ruleForm.validate((valid) => {
      if (valid) {
        console.log(this.instructionForm);
        const { editIndex, instructionForm } = this;
        this.update_launching({
          type: 'taskExternalList',
          data: cloneDeep(instructionForm),
          editIndex
        });
        this.$nextTick(() => {
          this.editIndex = -1;
          this.resetForm();
        });
      } else {
        return false;
      }
    });
  }
  resetForm() {
    this.instructionForm = {
      externalName: '',
      externalKey: '',
      allowSplit: 0,
      isInstances: '',
      isInstancesCondition: '',
      taskExternalParamList: [
        {
          id: 1,
          paramCode: '',
          paramName: '',
          paramType: '',
          required: '',
          paramDefault: '',
          edits: [0, 1, 1, 1, 1, 1, 0],
          addButton: true
        }
      ]
    };
  }
}
