


























import { Component, Vue } from 'vue-property-decorator';
import instruction from './lanchConfig/instruction.vue'; // 外部指令
import manual from './lanchConfig/manual.vue'; // 手工发起
import timing from './lanchConfig/timing.vue'; // 定时

@Component({
  components: { manual, timing, instruction }
})
export default class LaunchingMode extends Vue {
  private tabList: Array<any> = [
    {
      name: '手工发起',
      id: '1',
      component: 'manual'
    },
    {
      name: '定时发起',
      id: '2',
      component: 'timing'
    },
    {
      name: '外部指令发起',
      id: '3',
      component: 'instruction'
    }
  ];
  private tabActive = 'manual';
}
